.Youtube {
  opacity: 0;
  transform: translateY(100px);
  transition: 1s;

  .inner {
    > p {
      margin-top: 20px;
      width: 60%;
      line-height: 1.8;
      font-size: $fontM;
      color: #ddd;
    }

    .mainVids-wrap {
      position: relative;
      margin: 220px 0 150px;
      display: flex;
      justify-content: flex-end;

      .cont {
        position: relative;
        display: flex;
        align-items: flex-end;
        width: 40%;
        height: 400px;
        background-color: #fff;
        color: #222;
        padding: 40px;

        .info {
          position: absolute;
          top: 50px;
          left: 40%;
          line-height: 1.4;
          font-family: 'Dosis';
          font-size: $fontL;
          font-weight: 600;
        }

        .desc {
          font-size: $fontM;
          color: #777;
        }
      }

      .pic {
        position: absolute;
        top: -140px;
        left: 160px;
        width: 60%;
        height: 45vh;

        iframe {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }

        >span {
          position: absolute;
          top: -22px;
          left: -60px;
          font-size: $fontXXL;
          font-weight: 600;
          line-height: 1.4;
          letter-spacing: 5px;
          color: #fff;
        }

      }

      .line {
        position: absolute;
        bottom: 30px;
        left: 0;
        width: 35%;
        height: 3px;
        background-color: #fff;
      }

    }
    .wrap {
      margin-top: 70px;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      
      article {
        position: relative;
        width: 100%;
        border-top: 1px solid #ddd;
        padding: 20px 0;
        display: flex;
        flex-wrap: wrap;

        >span {
          display: block;
          width: 15%;
          font-size: $fontL;
          font-weight: 600;
        }
  
        .pic {
          width: 40%;
          height: 250px;
          cursor: pointer;
          transition: 0.5s;
          filter: grayscale(1);
  
          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
          }

          &:hover {
            filter: grayscale(0);
          }
        }

        .txt-wrap {
          width: 45%;
          padding-left: 20px;

          h2 {
            font-size: $fontXL;
          }

          p {
            margin-top: 15px;
            word-break: break-word;
            color: #ddd;
            font-size: $fontL;

          }
        }

        &:last-child {
          border-bottom: 1px solid #ddd;
        }
      }
    }
  }
  
  &.on {
    opacity: 1;
    transform: translateY(0);
  }
}

@media screen and (max-width:$tablet) {
  .Youtube {
    .inner {
      .mainVids-wrap {
        margin: 100px 0;

        .cont {
          display: none;
        }
        .pic {
          position: relative;
          width: 100%;
          top: 0;
          left: 0;
          margin: 0 auto;

          > span {
            display: none;
          }
        }
        .line {
          display: none;
        }
      }

      .wrap {
        article {
          >span {
            position: absolute;
            right: 0;
            bottom: 20px;
            width: auto;
          }
          .pic {
            width: 50%;
          }
  
          .txt-wrap {
            width: 50%;
          }
        }
      }
    }
  }
}

@media screen and (max-width:$mb) {
  .Youtube {
    .inner {
      .wrap {
        article {
          >span {
            position: absolute;
            right: 0;
            bottom: 20px;
            width: auto;
          }
          .pic {
            width: 100%;
          }
  
          .txt-wrap {
            width: 100%;
            margin-top: 20px;

            p {
              padding-bottom: 20px;
            }
          }
        }
      }
    }
  }
}